





























































































































































.blocks {
  .block {
    display: block;
    padding: 1rem;
    background: #222831;
    border: 1px solid #252a32;
    box-sizing: border-box;
    box-shadow: 0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0196802),
      0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725),
      0px 12.5216px 10.0172px rgba(0, 0, 0, 0.035),
      0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275),
      0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0503198),
      0px 100px 80px rgba(0, 0, 0, 0.07);
    border-radius: 2px;

    a {
      display: block;
    }

    .toggler {
      position: relative;

      .icon {
        position: absolute;
        top: 0;
        bottom:0;
        right: .75rem;
        margin: auto 0;
        width: 1rem;
      }
    }

    ul {
      margin: 0;
      padding: 0;
      list-style: none;
    }
  }

  &:not(.colored) .block {
    border-left: 4px solid #3b434f;
  }
}

.colored .icon {
  margin-right: 1rem;
}

.structures {
  .structure {
    margin-bottom: 1.5rem;
    padding: 1.5rem;

    img {
      width: 1.75rem;
      height: auto;
    }
    .custom-icon {
      background-size: 100%;
      background-repeat: no-repeat;
    }
    .name {
      font-weight: bold;
    }
    .balance {
      margin-left: 2.5rem;
    }
    .status {
      display: inline-block;
      vertical-align: middle;
    }
  }
}
