              <stop stop-color="#1FBA7C"/>
              <stop offset="1" stop-color="#0D95E1"/>
            </linearGradient>
              
            <linearGradient id="linear" ref="gradient">
              <stop ref="gray"   offset="99%" stop-color="#3B434F"/>
              <stop ref="center" offset="99%" stop-color="#0D95E1" />
              <stop ref="end"    offset="99%" stop-color="#1FBA7C" />
