        </svg>
      .arrow(ref="arrow")
    
    p.is-ultralight {{ t('Transactions per second') }}
    p.numbers
      | {{ current }}
      | 
      span.is-ultralight / {{ max }}
    p.small {{ t('Now') }} 
      span.is-ultralight / {{ t('Maximum') }}
</template>

<script>
export default {
  name: "Speedometer",
  props: {
    min: {
      default: 0
    },
    max: {
      default: 4369
    },
    current: {
      default: 0
    }
  },
  data() {
    return {
      deg: -35
    };
  },
  computed: {
    middle() {
      return parseFloat(
        parseFloat(this.current) / (parseInt(this.max) / 100)
      ).toFixed(2);
    }
  },
  mounted() {
    if (this.$refs.arrow)
      this.$refs.arrow.style.transform =
        "rotateZ(" + parseInt(this.deg + parseInt(this.middle)) + "deg)";
  }
};
</script>

