<template lang="pug">
  .container.explorer
    section#status.section.top-block
      .container
        .radial-block.linear-border.large-shadow
          .columns.is-vcentered
            .column
              .columns.is-vcentered
                .column.is-narrow.is-hidden-mobile
                  SvgIcon.symbol(name="symbol")
                .column
                  .columns.is-multiline.is-flex-mobile.is-variable.is-2
                    .column.is-4-tablet-only.is-4-mobile(v-for="(item, i) in stakeStats" :key="i")
                      Widget(
                        type="transparent"
                        :number="item.number"
                        :numbers="item.numbers"
                        :label="item.label"
                        :suffix="item.suffix"
                        :linkTitle="item.linkTitle"
                        :linkTo="item.linkTo"
                      )
                    .column.is-narrow
                      .card.transparent
                        .card-content
                          .label.is-spaced(v-translate) Number of Structures
                          .count
                            | {{ parseInt(stats.status.structures_count) }}
                            |
                            a(@click="changeTab(5)") {{ t('More') }}

                    .column.is-1-tablet.is-hidden-desktop
              Widgets(:stats="currencyStats" :gapped="1")

                .column.is-1-tablet.is-hidden-desktop
            .column.is-narrow.is-hidden-touch.is-hidden-tablet-only
              Speedometer(
                min="0"
                max="4369"
                :current="stats.transactions.per_second"
              )

    section.section
      .container
        .columns.is-multiline.is-flex-mobile
          .column
            Widget(
              :number="parseInt(stats.blocks.seconds_between_blocks)"
              :suffix="' ' + t('sec')"
              :label="t('Block time')"
            )
          .column
            Widget(
              :number="stats.blocks.transactions_in_last_block"
              :label="t('Transactions in block')"
            )
          .column
            Widget(
              :number="stats.transactions.per_min"
              :label="t('Transactions per min')"
            )
          .column
            Widget(
              :number="stats.transactions.per_hour"
              :label="t('Transactions per hour')"
            )
          .column
            Widget(
              :number="stats.transactions.per_day"
              :label="t('Daily transactions')"
            )

    section.section
      .container.is-relative
        .search-block.relative
          .colored-block
            input.input(
              v-model="keyword"
              @change="search"
              @paste="search"
              :placeholder="t('Search for address, tx, block')"
            )
          .icon
            SvgIcon.icon(name="magnify")
        b-tabs#tabs(
          v-model="tab"
          @input="changeRoute"
          :animated="false"
          destroy-on-hide
          multiline
        )
          b-tab-item(:label="t('Blocks')")
            Blocks
          b-tab-item(:label="t('Transactions')")
            Transactions
          b-tab-item(:label="t('Price')")
            Price
          b-tab-item(:label="t('GitLab')")
            GitHub
          b-tab-item(:label="t('Addresses')")
            Addresses
          b-tab-item(:label="t('Structures')")
            Structures

</template>

<script>
// @ is an alias to /src
import SvgIcon from "@/components/common/SvgIcon";
import CustomIcon from "@/components/common/CustomIcon";
import GrowingNumber from "@/components/common/GrowingNumber";

import Widget from "@/components/common/Widget";
import Widgets from "@/components/common/Widgets";
import Speedometer from "@/components/common/Speedometer";

import Blocks from "@/components/explorer/tabs/Blocks";
import Transactions from "@/components/explorer/tabs/Transactions";
import Price from "@/components/explorer/tabs/Price";
import GitHub from "@/components/explorer/tabs/GitHub";
import Addresses from "@/components/explorer/tabs/Addresses";
import Structures from "@/components/explorer/tabs/Structures";

import { mapGetters } from "vuex";
import router from "@/router";

export default {
  name: "Home",
  components: {
    SvgIcon,
    CustomIcon,
    Widget,
    Widgets,
    Speedometer,
    Blocks,
    Transactions,
    Price,
    GitHub,
    Addresses,
    Structures,
    GrowingNumber
  },
  data() {
    return {
      keyword: null,
      loading: false,
      tab: 0,
      routes: [
        "/blocks",
        "/transactions",
        "/prices",
        "/github",
        "/addresses",
        "/structures"
      ]
    };
  },
  computed: {
    ...mapGetters(["stats", "currency", "locale"]),
    stakeStats() {
      return [
        {
          number: this.stats.status.coins / 100,
          label: this.$translate.text("Total coins")
        },
        {
          number: this.stats.status.staking / 100,
          label: this.$translate.text("In staking")
        },
        {
          number: 18000000,
          label: this.$translate.text("Initial Emission")
        },
        {
          number: parseInt(this.stats.status.percent),
          label: this.$translate.text("Staking Percentage"),
          suffix: "%"
        }
      ];
    },
    currencyStats() {
      return [
        {
          number: this.stats.transactions.summary || 0,
          label: this.$translate.text("Transactions"),
          customClass: "is-narrow is-6-mobile"
        },
        {
          number: this.stats.blocks.summary || 0,
          label: this.$translate.text("Blocks"),
          customClass: "is-narrow is-6-mobile"
        },
        {
          label:
            this.$translate.text("Trading Volume UMI") + "/" + this.currency,
          labelLink: 'https://umi.top/volumes',
          numbers: [
            {
              value: this.toUmi(this.stats.volume_all)
            },
            {
              prefix: " / ",
              number: this.stats.volume_all,
              convertToFiat: true,
              suffix: ""
            }
          ],
          customClass: "is-12-mobile is-narrow-tablet-only  is-narrow is-3-desktop"
        },
        {
          label: this.$translate.text("Liquidity") + ' (' + this.$translate.text("buy") + '/' + this.$translate.text("sell") + ')',
          labelLink: 'https://umi.top/liquidity',
          numbers: [
            {
              value: this.stats.liquid ? this.convert(this.stats.liquid.bids[this.currency], this.currency) : 0,
              suffix: '/'
            },
            {
              value: this.stats.liquid ? this.convert(this.stats.liquid.asks[this.currency], this.currency) : 0,
            }
          ],
          customClass: "is-12-mobile is-narrow-tablet-only"
        },
        {
          numbers: [
            {
              number: parseFloat(this.stats.status.coins),
              rounded: true,
              convertToFiat: true
            }
          ],
          label: this.$translate.text("Capitalization"),
          customClass: "is-narrow is-12-mobile"
        }
      ];
    }
  },
  props: {
    activeTab: {
      default: 0
    }
  },
  methods: {
    changeRoute(tab) {
      router.push(this.routes[tab]);
    },
    changeTab(tab) {
      this.tab = tab;
      document
        .getElementById("tabs")
        .scrollIntoView({ behavior: "smooth", inline: "nearest" });
    },
    search() {
      this.find(this.keyword);
    }
  },
  mounted() {
    this.tab = this.activeTab;
  }
};
</script>

