<template lang="pug">
  div
    Widgets(:stats="transactionsStats")
    br
    Currencies
    .transactions
      Trx(
        v-if="data"
        v-for="(trx, i) in data"
        :key="i"
        :trx="trx"
      )

    Pagination(
      @change="pagination"
      :perPage="params.limit"
      :total="stats.transactions.summary"
    )
</template>

<script>
import Widgets from "@/components/common/Widgets";
import Currencies from "@/components/common/Currencies";
import Pagination from "@/components/common/Pagination";
import Trx from "@/components/explorer/Trx";
import { mapGetters } from "vuex";
import api from "@/api/explorer";

export default {
  name: "TransactionsTab",
  components: { Widgets, Currencies, Pagination, Trx },
  computed: {
    ...mapGetters(["stats", "currency"]),
    transactionsStats() {
      return [
        {
          label: this.$translate.text("Total transactions"),
          number: this.stats.transactions.summary || 0,
          customClass: "is-6-mobile is-4-tablet is-3-desktop"
        },
        {
          label: this.$translate.text("24 hours transaction"),
          number: this.stats.transactions.per_day || 0,
          customClass: "is-6-mobile is-4-tablet is-4-desktop"
        },
        {
          label: this.$translate.text("Transferred in 24 hours"),
          numbers: [
            {
              number: this.stats.transactions.value_per_day || 0,
              suffix: "UMI"
            },
            {
              prefix: " / ",
              number: this.stats.transactions.value_per_day || 0,
              convertToFiat: true
            }
          ],
          customClass: "is-12-mobile is-6-tablet is-5-desktop"
        },
        {
          label: this.$translate.text("Transactions per hour (average)"),
          number: parseFloat((this.stats.transactions.per_day / 24).toFixed(2)),
          customClass: "is-12-mobile is-narrow"
        },
        {
          label: this.$translate.text("Transferred per hour (average)"),
          numbers: [
            {
              number: this.stats.transactions.value_per_hour,
              suffix: "UMI"
            },
            {
              prefix: " / ",
              number: this.stats.transactions.value_per_hour,
              convertToFiat: true
            }
          ],
          customClass: "is-12-mobile is-narrow"
        },
        {
          label: this.$translate.text(
            "100 largest transactions in the last 24 hours"
          ),
          numbers: [
            {
              number: this.stats.transactions.top_100_value_per_day || 0,
              suffix: "UMI"
            },
            {
              prefix: " / ",
              number: this.stats.transactions.top_100_value_per_day || 0,
              convertToFiat: true
            }
          ],
          customClass: "is-12-mobile"
        }
      ];
    }
  },
  data() {
    return {
      loading: false,
      data: [],
      params: {
        limit: 10,
        offset: 0
      },
      interval: null
    };
  },
  methods: {
    pagination(params) {
      if(params && params.offset >=0 && params.limit > 0) {
        this.params = params;
        this.fetch();
      }
    },
    fetch() {
      this.loading = true;
      api
        .getTransactions(this.params)
        .then(({ data }) => {
          this.data = data.data;
          this.loading = false;
        })
        .catch(() => {
          this.data = [];
          this.loading = false;
        });
    }
  },
  created() {
    this.fetch();
    this.interval = setInterval(() => {
      this.fetch();
    }, 6000);
  },
  beforeDestroy: function() {
    clearInterval(this.interval);
  }
};
</script>

<style scoped>
.transactions {
  margin-bottom: 1.5rem;
}
</style>
