<template lang="pug">
  div.blocks-tab
    Widgets(:stats="blocksStats")
    br
    Currencies
    b-table.table.is-striped.is-hoverable.is-fullwidth(
      :data="data"
      :loading="loading"
      
      paginated
      backend-pagination
      :total='total'
      :per-page='perPage'
      @page-change="onPageChange"
      :current-page.sync="page"
      range-before="3"
      range-after="3"

      :mobile-cards='true'
      :default-sort="['height', 'desc']"
    )
      template( slot-scope="props" )
        b-table-column(
          field="height"
          :label="t('Height')"
          :centered="true"
        )
          router-link.hash(:to="{ name: 'Block', params: { height: props.row.height }}") {{ props.row.height }}
        b-table-column(
          field="transactions" 
          :label="t('Transactions')"
          :centered="true"
          :numeric="true"
        ) {{ props.row.tx_count }}
        //- b-table-column(
        //-   field="amount" 
        //-   :label="t('Amount') + ' (UMI)'"
        //-   :numeric="true"
        //- ) {{ toUmi(props.row.amount + props.row.fee_amount) }}
        //- b-table-column(
        //-   field="amount" 
        //-   :label="t('Amount') + ' (' + currency + ')'"
        //-   :numeric="true"
        //- ) {{ pretty(props.row.amount + props.row.fee_amount, false) }}
        b-table-column(
          field="date" 
          :label="t('Timestamp')"
          :numeric="true"
        ) {{ props.row.created_at | moment("DD.MM.YY HH:mm:ss") }}
        b-table-column(
          field="hash" 
          label="Hash"
          :numeric="true"
        ) 
          router-link.hash(:to="{ name: 'Block', params: { height: props.row.height }}") {{ cut(props.row.hash) }}
        b-table-column.is-actions-cell.is-hidden-mobile(custom-key='actions' width="40px")
          .buttons.is-right
            .button.is-primary.is-small(:title="t('Copy tx')" v-clipboard:copy="props.row.hash" v-clipboard:success="onCopy")
              SvgIcon.icon(name="content-copy-white")

</template>

<script>
import Widgets from "@/components/common/Widgets";
import Currencies from "@/components/common/Currencies";
import SvgIcon from "@/components/common/SvgIcon";

import { mapGetters } from "vuex";
import api from "@/api/explorer";

export default {
  name: "BlocksTab",
  components: { Widgets, Currencies, SvgIcon },
  data() {
    return {
      data: [],
      loading: false,
      page: 1,
      perPage: 15,
      interval: null
    };
  },
  computed: {
    ...mapGetters(["stats", "currency"]),
    total() {
      return this.stats.blocks.summary || 0;
    },
    blocksStats() {
      return [
        {
          number: this.total,
          label: this.$translate.text("Number of blocks"),
          customClass: 'is-6-mobile is-4-tablet is-4-desktop'
        },
        {
          timestamp: this.stats.blocks.created_at,
          label: this.$translate.text("Last block time"),
          customClass: 'is-6-mobile is-4-tablet is-4-desktop'
        },
        {
          number: parseFloat(this.stats.blocks.transactions_per_block),
          label: this.$translate.text("Transactions in block (24 hours)"),
          customClass: 'is-6-mobile is-4-tablet is-4-desktop'
        },
        {
          number: parseInt(this.stats.blocks.seconds_between_blocks),
          label: this.$translate.text("Since the last block has passed"),
          suffix: " " + this.$translate.text("sec"),
          customClass: 'is-6-mobile is-4-tablet is-4-desktop'
        },
        {
          number: this.stats.blocks.active_addresses || 0,
          label: this.$translate.text("Active addresses (24 hours)"),
          customClass: 'is-6-mobile is-4-tablet is-4-desktop'
        },
        {
          number: this.stats.blocks.blocks_per_hour,
          label: this.$translate.text("Blocks per hour (24 hours)"),
          customClass: 'is-6-mobile is-4-tablet is-4-desktop'
        }
      ];
    }
  },
  methods: {
    loadAsyncData() {
      this.loading = true;
      let params = {
        limit: this.perPage,
        offset: this.perPage * (this.page - 1)
      };
      api
        .getBlocks(params)
        .then(({ data }) => {
          this.data = data.data;
          this.loading = false;
        })
        .catch(() => {
          this.data = [];
          this.loading = false;
        });
    },
    onPageChange(page) {
      this.page = page;
      this.loadAsyncData();
    }
  },
  created() {
    if(this.$route.query && this.$route.query.page)
      this.page = parseInt(this.$route.query.page)
    else
      this.page = 1
    this.loadAsyncData();
    this.interval = setInterval(() => {
      this.loadAsyncData();
    }, 6000);
  },
  beforeDestroy: function() {
    clearInterval(this.interval);
  },
  watch:{
    page() {
      this.$router
        .push({
          query: {
            ...this.$route.query,
            page: this.page || 1
          }
        })
        .catch((err) => {
          console.log(err)
        });
    },
  }
};
</script>

<style scoped>
.blocks-tab {
  min-height: 20rem;
}
</style>
