<template lang="pug">
  div
    .content.is-size-4 {{ t('The main GitLab with documentation and SDK') }}
      a.icon(href="https://gitlab.com/umitop" target="_blank")
        SvgIcon(name="open-in-new")
    Widgets(:stats="githubStats")

</template>

<script>
import Widgets from "@/components/common/Widgets";
import SvgIcon from "@/components/common/SvgIcon";
const fetch = require('node-fetch');

export default {
  name: "GitHubTab",
  components: { Widgets, SvgIcon },
  data() {
    return {
      stats: {
        "stars": 32,
        "updated_at": "2020-09-16T03:14:37Z",
        "reps": 13,
        "followers": 27
      }
    }
  },
  computed: {
    githubStats() {
      return [
        {
          number: this.stats.stars,
          label: this.$translate.text("Stars"),
          customClass: "is-6-mobile is-3-tablet"
        },
        {
          timestamp: this.stats.updated_at || 0,
          label: this.$translate.text("Last update"),
          customClass: "is-6-mobile"
        },
        {
          number: this.stats.reps,
          label: this.$translate.text("Repositories"),
          customClass: "is-6-mobile is-narrow-tablet"
        },
        {
          number: this.stats.followers,
          label: this.$translate.text("Followers"),
          customClass: "is-6-mobile is-3-tablet"
        }
      ];
    }
  },

  mounted() {
    const params = {
      headers: {
        'Authorization': `token 7370cfc6346df6cc6e0a9f60bc7b08e19148a8a4`
      }
    }
    // https://gitlab.com/api/v4/users/6512882
    fetch('https://api.github.com/users/umitop/repos', params)
      .then(res => res.json())
      .then(json => {
       if(json.length > 0) {
        this.stats['stars'] = json.reduce((acc, rep) => acc + rep.stargazers_count, 0)
        this.stats['updated_at'] = new Date(Math.max.apply(null, json.map(function(e) {
          return new Date(e.pushed_at);
        })));
       }
      })

    fetch('https://api.github.com/users/umitop', params)
      .then(res => res.json())
      .then(json => {
        this.stats['reps'] = json.public_repos
        this.stats['followers'] = json.followers
      })
  }
};
</script>

<style scoped>
  .icon {
    margin-left: .75rem;
    vertical-align: text-top;
    cursor: pointer;
  }
</style>
