<template lang="pug">
  .card(:class="type")
    .card-content
      .label.is-spaced(v-if="label")
        | {{ label }}
        a.icon-link(v-if="labelLink" :href="labelLink")
          SvgIcon.icon(name="open-in-new")
      .count
        template(v-if="numbers")
          span(v-for="(item, i) in numbers" :key="i")
            growing-number(
              v-if='item.number != null'
              :value='item.number'
              :rounded='item.rounded'
              :prefix='item.prefix'
              :suffix='item.suffix'
              :convertToFiat='item.convertToFiat'
            )
            span.value(v-else)
              | {{ item.prefix }} {{ item.value }} {{ item.suffix }}

        growing-number(
          v-else-if="number !== null"
          :value='number'
          :prefix='prefix'
          :suffix='suffix'
          :convertToFiat='convertToFiat'
        )
        div.timestamp(
          v-else-if="timestamp"
        )
          | {{ timestamp | moment("DD.MM.YY HH:mm:ss [GMT] Z") }}
        div.value(v-else)
          | {{ value }}
        router-link(v-if="linkTitle" :to="linkTo") {{ linkTitle }}

</template>

<script>
import GrowingNumber from "@/components/common/GrowingNumber";
import SvgIcon from "@/components/common/SvgIcon";
export default {
  name: "Widget",
  components: { GrowingNumber, SvgIcon },
  methods: {
    isNumber() {
      return this.number.isFinite();
    }
  },
  props: {
    type: {
      type: String,
      default: "is-card-widget"
    },
    number: {
      type: Number,
      default: null
    },
    numbers: {
      type: Array,
      default: function() {
        return null;
      }
    },
    convertToFiat: {
      type: Boolean,
      default: false
    },
    timestamp: {
      default: null
    },
    value: {
      type: String,
      default: null
    },
    prefix: {
      type: String,
      default: null
    },
    suffix: {
      type: String,
      default: null
    },
    label: {
      type: String,
      default: null
    },
    labelLink: {
      type: String,
      default: null
    },
    linkTitle: {
      type: String,
      default: null
    },
    linkTo: {
      type: String,
      default: null
    }
  }
};
</script>

<style scoped>
  .icon-link {
    position: absolute;
    right: .25rem;
    top: .25rem;
  }
  .icon-link .icon {
    width: 1rem;
    height: 1rem;
  }
</style>
